.Mic {
    display: inline-flex;
    background: #d1d3d7;
    border-radius: 50%;
    text-align: center;
    align-items: center;
    justify-content: center
}
.Mic.bottom {
    position: absolute;
    bottom: 5%;
    margin-left: -22.5px
}
.Mic.big {
    width: 90px;
    height: 90px
}
.Mic.normal {
    width: 45px;
    height: 45px
}
.Mic.speaking {
    background: #75d937;
    -webkit-animation-name: speaking;
    animation-name: speaking;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite
}
@-webkit-keyframes speaking {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1)
    }
    50% {
        -webkit-transform: scale(1.2);
        transform: scale(1.2)
    }
    to {
        -webkit-transform: scale(1);
        transform: scale(1)
    }
}
@keyframes speaking {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1)
    }
    50% {
        -webkit-transform: scale(1.2);
        transform: scale(1.2)
    }
    to {
        -webkit-transform: scale(1);
        transform: scale(1)
    }
}

@media (max-width: 543px) {
  .Mic.bottom {
    bottom: 10%;
  }
}

.SpokenWord {
    font-size: 94px;
    line-height: 94px;
    font-weight: 700;
    text-align: center;
    color: #3a3d43
}
@media (max-width: 543px) {
    .SpokenWord {
        font-size: 48px;
        line-height: 48px
    }
}

.Logo {
    position: absolute;
    width: 100%;
    top: 5%;
    text-align: center;
    font-size: 24px;
    font-weight: 700
}
.Logo a {
    text-decoration: none;
    color: #2b2d30
}

.Copyright {
  position: absolute;
  bottom: 5px;
  right: 20px;
  font-size: 14px;
  width: 100%;
  text-align: right;
}

.Copyright a {
  color: #7a7a7a;
  text-decoration: none;
}

.Copyright a:hover {
  text-decoration: underline;
}

@media (max-width: 543px) {
  .Copyright {
    right: 0;
    text-align: center;
  }
}

#root,
.app-container,
.game-container,
body,
html {
    height: 100%
}
body {
    margin: 0;
    padding: 0;
    font-family: PT Serif, serif;
    font-size: 28px;
    line-height: 40px;
    color: #7c7f83
}
.game-container {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center
}
.game-inner {
    width: 60%
}
.gap-bottom-xsmall {
    margin-bottom: 10px
}
.gap-bottom-small {
    margin-bottom: 20px
}
.gap-bottom {
    margin-bottom: 30px
}
.gap-bottom-large {
    margin-bottom: 40px
}
.pad-bottom {
    padding-bottom: 30px
}
.pad-bottom-large {
    padding-bottom: 40px
}
h1 {
    font-size: 54px;
    font-weight: 700;
    color: #3a3d43;
    line-height: 58px
}
.text-primary {
    color: #2b2d30;
    letter-spacing: -.5px
}
.color-green {
    color: #75d937
}
.btn {
    font-size: 24px;
    background: #75d937;
    color: #fff;
    font-weight: 700;
    border: none;
    font-family: PT Serif, serif;
    line-height: 24px;
    padding: 17px 54px;
    border-radius: 100px
}
.btn:focus {
    outline: none
}
.btn:hover {
    background: #5cc01e;
    cursor: pointer
}
@media (max-width: 543px) {
    body {
        font-size: 18px;
        line-height: 26px
    }
    h1 {
        font-size: 48px
    }
    .game-inner {
        width: 90%
    }
}

